.footer{background-color:#fff;border-top:1px solid #eaeaea;padding:2rem 0;font-family:Arial,sans-serif;font-size:.9rem;color:#4a4a4a}.footer .container{max-width:1200px;margin:0 auto;padding:0 1rem}.footer .content{display:flex;justify-content:space-between;margin-bottom:1.5rem}.footer .content .section{flex:1;margin-right:2rem}.footer .content .section:last-child{margin-right:0}.footer .content .section ul{list-style:none;padding:0}.footer .content .section ul li{margin-bottom:.5rem}.footer .content .section ul li a{text-decoration:none;color:#4a4a4a}.footer .content .section ul li a:hover{color:#0070f3}.footer .content .section .social{display:flex;gap:1rem}.footer .content .section .social a{color:#4a4a4a;font-size:1.2rem}.footer .content .section .social a:hover{color:#0070f3}.footer .copyright{text-align:center;border-top:1px solid #eaeaea;padding-top:1rem;color:#a0a0a0}